// borders

.b-all {
  border: 1px solid @gray-light;
}

hr.paystub-title-break {
  height: 10px;
  border: 0;
  box-shadow: 0 10px 10px -10px #8c8b8b inset;
  border-radius: 10px;
}


// fonts

.strong {
  font-weight: bold;
}




// colors

.bg-blue {
  background: #336699 !important;
  color: #ffffff !important;
}

.bg-white {
  background: #fff !important;
}

.bg-gray-light {
  background-color: @gray-light !important;
}


.editable {
  border: 1px dashed darken(@gray-light, 10%);
}


.input-transparent {
  background: transparent;
  border: 1px dashed @gray-light;
}


.paystub-header {
  border-collapse: separate;
  border-radius: 5px;
  background-color: #006DCC;
  background-image: linear-gradient(to bottom, #00abff, #006DCC);
  color: white;

  h1 {
    padding-left: 5px;
    padding-top: 15px;
  }

  small {
      color: #EEEEEE;
  }

  .row {
    padding: 5px;
  }
}


.paystub-break {
  clear: both;
  width: 100%;
  background-color: #939393 !important;
  height: 4px;
  border-radius: 3px;
  margin-top: 5px;
  margin-bottom: 5px;
}


.datepicker-hot {
  position: relative;
  z-index: 900;
}

.nav .navbar-nav .navbar-right {
  z-index: 901;
}


.paystub {


  table {
    border-collapse: separate;
    border-radius: 0 5px;

    thead {


      th {
        background-color: #006DCC;
        color: white;

      }
    }


    tbody {


      td {
        background-color: #F0F8FF;
      }
    }

    tr {

      td:first-child,
      th:first-child {
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
      }

      td:last-child,
      th:last-child {
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
      }
    }
  }
}


.navbar-title-text {
  color: #FFF !important;
}

li .navbar-title-text {
  color: #000000 !important;
}


.overridesTable {
  max-width: 900px;
  max-height: 500px;
}


// footer
html, body {
  height: 100%;
  background: linear-gradient(to bottom, #e9e9e9 0%, #f0f0f0 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.wrapper {
  min-height: 100%; 
  height: auto !important; 
  height: 100%;
}

.site-footer {
  height: 40px;
}

// hyperlink styles

.icon-link {
  color: inherit;
  text-decoration: none;
  border: 0;
  padding: 2px;

    &:hover {
      color: #919191;
      cursor: pointer;
    }
}

.control-label {
  font-size: 16px;
}

.jsdata {
  display: none !important;
}