.paystub-header {
  border-collapse: separate;
  border-radius: 5px;
  background-color: #006DCC;
  background-image: linear-gradient(to bottom, #00abff, #006DCC);
  color: white;
}
.paystub-header h1 {
  padding-left: 5px;
  padding-top: 15px;
}
.paystub-header small {
  color: #EEEEEE;
}
.paystub-header .row {
  padding: 5px;
}
.paystub-break {
  clear: both;
  width: 100%;
  background-color: #939393 !important;
  height: 4px;
  border-radius: 3px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.datepicker-hot {
  position: relative;
  z-index: 900;
}
.nav .navbar-nav .navbar-right {
  z-index: 901;
}
.paystub table {
  border-collapse: separate;
  border-radius: 0 5px;
}
.paystub table thead th {
  background-color: #006DCC;
  color: white;
}
.paystub table tbody td {
  background-color: #F0F8FF;
}
.paystub table tr td:first-child,
.paystub table tr th:first-child {
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
}
.paystub table tr td:last-child,
.paystub table tr th:last-child {
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
}
.navbar-title-text {
  color: #FFF !important;
}
li .navbar-title-text {
  color: #000000 !important;
}
.overridesTable {
  max-width: 900px;
  max-height: 500px;
}
html,
body {
  height: 100%;
  background: linear-gradient(to bottom, #e9e9e9 0%, #f0f0f0 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%;
}
.site-footer {
  height: 40px;
}
.icon-link {
  color: inherit;
  text-decoration: none;
  border: 0;
  padding: 2px;
}
.icon-link:hover {
  color: #919191;
  cursor: pointer;
}
.control-label {
  font-size: 16px;
}
.jsdata {
  display: none !important;
}
